.HeadersFooters{background-color:#d4d4d4;position:relative;width:100%;height:200vh}.HeadersFooters>div{width:100%}

.Navbar{height:70px;background-color:white}.Navbar-container{display:flex;justify-content:space-between;align-items:center}.Navbar-container .company-name{display:flex;align-items:center;height:70px}.Navbar-container .company-name img{margin-right:13px}.Navbar-container .Navbar-buttons{display:flex;align-items:center}.Navbar-container .Navbar-buttons a{margin:10px;text-decoration:none;display:flex;align-items:center}.Navbar-container .Navbar-buttons a img{margin-left:5px}.Navbar-container .Navbar-buttons p{color:rgba(31,32,65,0.5)}.Navbar-container .Navbar-buttons .line{width:1px;height:30px;margin:0 20px 0 10px;background:rgba(31,32,65,0.1)}

.Footer{background-color:white}.Footer .description{height:262px;display:flex;justify-content:space-between;padding-top:100px}.Footer .description .company-name{display:flex;align-items:center;height:70px}.Footer .description .company-name img{margin-right:13px}.Footer .description>:nth-child(1),.Footer .description>:nth-child(5){width:260px}.Footer .description>:nth-child(1) p,.Footer .description>:nth-child(5) p{margin-bottom:20px}.Footer .description>:nth-child(2) p,.Footer .description>:nth-child(3) p,.Footer .description>:nth-child(4) p{color:rgba(31,32,65,0.5)}.Footer .description>div p{margin-top:20px}.Footer-outline{height:2px;background-color:rgba(31,32,65,0.25)}.Footer .policy{height:70px;display:flex;justify-content:space-between;align-items:center}.Footer .policy img{margin:10px}

.FooterMobile{width:768px;height:367px;background-color:white;display:flex;align-items:center;flex-direction:column;justify-content:center}.FooterMobile .company-name{display:flex;align-items:center;height:70px}.FooterMobile .company-name img{margin-right:13px}.FooterMobile>div{height:70px;display:flex;align-items:center}.FooterMobile>div img{margin:10px}

.LandingPage-info{position:relative;background-size:cover;height:900px}.LandingPage-info>div{padding-top:70px}.LandingPage-info>p{position:absolute;width:300px;bottom:18px;right:150px;text-align:right}

.RegistrationPage-info{position:relative;display:flex;align-items:center;justify-content:center;background-size:cover;height:685px}

.LoginPage-info{position:relative;display:flex;align-items:center;justify-content:center;background-size:cover;height:685px}

