.FindNumbers {
  width: 380px;
  height: 374px;
  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  .date {
    position: relative;
    &-dropdown {
      display: flex;
      justify-content: space-between;
      margin-bottom: 5px;
    }
    .Calendar {
      position: absolute;
      top: 1;
      z-index: 99;
    }
  }
}
